//mockjs
const Mock = require('mockjs');
//mock.Random
const Random = Mock.Random;
export default {
    //
    newsData:function(){
        let ret = {
		  "data": [],
		  "status": 200,
		  "success": true
		}
	    for(let i = 0; i < 10; i++){
	        let newObject = {
	            title: Random.csentence(5, 30), //
	            image: Random.dataImage('300x300', 'mock'), // 300X300
	            author: Random.cname(), // 
	            datetime: Random.date() + ' ' + Random.time() // ,yyyy-MM-ddRandom.time() 
	        }
	        ret.data.push(newObject)
	    }
        return ret;
    }
}