import Vue from 'vue'
import Router from 'vue-router'
import MainPart from '@/views/mainPart'
Vue.use(Router)

// 
export const constantRouterMap = [
  {
    path: '/drag-table',
    name: 'drag-table',
    redirect: 'index',
    component: MainPart,
    // hidden: true,
    children: [
      {
        path: 'index',
        name: 'index',
        meta: {
          // roles: [''],
          title: 'ts',
          icon: 'el-icon-setting'
        },
        component: () => import(/* webpackChunkName: "about" */ '@/views/drag-table/drag-table')
      }
      
    ]
  },
  {
    path: '/echarts-china',
    name: 'echarts-china',
    redirect: 'index',
    component: MainPart,
    // hidden: true,
    children: [
      {
        path: 'index',
        name: 'index',
        meta: {
          // roles: [''],
          title: '',
          icon: 'el-icon-setting'
        },
        component: () => import(/* webpackChunkName: "about" */ '@/views/echarts/echartsChina')
      }
      
    ]
  },
  {
    path: '/login',
    name: 'login',
    hidden: true,
    meta: {
      roles: [''],
      title: '',
      icon: 'el-icon-setting'
    },
    component: () => import(/* webpackChunkName: "about" */ '@/views/login/Login')
  }
]

// 
export const asyncRouterMap = [
  {
    path: '',
    component: MainPart,
    // component: () => import('@/views/home/Home'),
    redirect: 'home',
    meta: {
      roles: [''],
      title: '',
      icon: 'el-icon-document'
    },
    children: [{
      path: 'home',
      name: 'Home',
      component: () => import('@/views/home/Home'),
      meta: {
        roles: [''],
        title: '',
        icon: 'el-icon-document'
      }
    },
    {
      path: 'message',
      name: 'message',
      meta: {
        roles: [''],
        title: '',
        icon: 'el-icon-setting'
      },
      component: () => import(/* webpackChunkName: "about" */ '@/views/message/Message'),
      children: [
        {
          path: 'message2',
          name: 'message2',
          meta: {
            roles: [''],
            title: '2',
            icon: 'el-icon-setting'
          },
          component: () => import(/* webpackChunkName: "about" */ '@/views/message/Message')
        },
        {
          path: 'message3',
          name: 'message3',
          meta: {
            roles: [''],
            title: '3',
            icon: 'el-icon-setting'
          },
          component: () => import(/* webpackChunkName: "about" */ '@/views/message/Message')
        }
      ]
    }]
  },
  // {
  //   path: '/message',
  //   name: 'message',
  //   meta: {
  //     roles: [''],
  //     title: '',
  //     icon: 'el-icon-setting'
  //   },
  //   component: () => import(/* webpackChunkName: "about" */ '@/views/message/Message')
  // },
  {
    path: '/about',
    component: MainPart,
    meta: {
      roles: [''],
      title: '',
      icon: 'el-icon-setting'
    },
    children: [{
      path: 'index',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ '@/views/about/About.vue'),
      meta: {
        roles: [''],
        title: '',
        icon: 'el-icon-setting'
      }
    },
    {
      path: 'index2',
      name: 'about2',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ '@/views/about/About.vue'),
      meta: {
        roles: [''],
        title: '',
        icon: 'el-icon-setting'
      }
    }
    ]
  },
  {
    path: '/manager',
    component: MainPart,
    meta: {
      roles: [''],
      title: '',
      icon: 'el-icon-setting'
    },
    children: [{
      path: 'index',
      name: 'manager',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ '@/views/manager/Manager.vue'),
      meta: {
        roles: [''],
        title: '',
        icon: 'el-icon-setting'
      }
    }]
  }
]

// console.log(asyncRouterMap, constantRouterMap)
// asyncRouterMap.forEach(value => {
//   constantRouterMap.push(value)
// })

export default new Router({
  // mode: 'history', //
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})
